.page {
  background-color: #EFEFEF;
}
.tabbar-wrap {
  height: 3.125rem;
  position: fixed;
  bottom: 0;
  /* 距离底部为0像素 */
  left: 0;
  /* 左侧与视口边界对齐 */
  width: 100%;
  /* 宽度等于视口宽度 */
  z-index: 9999;
}
.home-page {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: transparent;
  color: #444;
}
.about-page {
  margin: 0.46875rem auto;
  padding: 1.5625rem;
  height: 100%;
  background-color: #fff;
}
.about-page .des-tx {
  background-color: #fff;
}
.about-page .des-tx h1 {
  font-size: 1.5625rem;
}
.about-page .des-tx p {
  line-height: 1rem;
  padding: 0.625rem;
}
.about-page .des-tx p img {
  display: inline;
  width: 1.5625rem;
  padding: 0.3125rem;
}
.about-page .des-tx img {
  width: 100%;
  margin: 0 auto;
}
.about-page .company {
  background-color: #fff;
  padding-left: 0.625rem;
}
.about-page .company h1 {
  padding-left: 0;
  font-size: 1.40625rem;
}
.about-page .company ul {
  padding: 0;
  margin: 0;
  width: 22.1875rem;
}
.about-page .company ul li {
  height: 1.5rem;
  line-height: 1.5rem;
  overflow: hidden;
  width: 22.1875rem;
}
.about-page .company ul li span {
  display: inline-block;
  padding-right: 0.15625rem;
  font-weight: 500;
  overflow: hidden;
  vertical-align: middle;
}
.about-page .company ul li a {
  display: inline-block;
}
.about-page .contact {
  background-color: #fff;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  display: flex;
  height: 5rem;
  position: relative;
  z-index: 9;
}
.about-page .contact span.wechat {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.about-page .contact span.wechat img {
  width: 2.5rem;
  display: inline;
}
.about-page .contact span.youtube {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.about-page .contact span.youtube img {
  width: 2.5rem;
  display: inline;
}
.about-page .contact span.facebook {
  display: inline-block;
  width: 30%;
  text-align: center;
}
.about-page .contact span.facebook img {
  width: 2.5rem;
  display: inline;
}
